

import { Link } from "react-router-dom";

// import Swiper core and required modules
import { Navigation, Pagination, A11y } from 'swiper/modules';
import { Swiper, SwiperSlide, useSwiper } from 'swiper/react';

import ParagrafHome from '../../componets/paragrafHome/ParagrafHome';

//icon
import prewArrow from "../../asset/img/icon/arrow  right_.svg";
import nextArrow from "../../asset/img/icon/arrow left_.svg";

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

// style
import "./choice.scss"

export default function Choice({props}){
    return(
        <div className="choice__wrapper swiper__wrapper _body__box__shadow">
            <ParagrafHome title="Выбор категории"/>
         <Swiper
            // install Swiper modules
            modules={[Navigation, Pagination, A11y]}
            spaceBetween={10}
            slidesPerView={4}
            breakpoints={{
              1050: {
                slidesPerView: 4,
              },
              500: {
                slidesPerView: 3,
              },
              320: {
                slidesPerView: 2,
              },
            }}
            pagination={{ 
              clickable: true,
              dynamicBullets: true, 
            }}
            loop = {true}
            >
            <SwiperNavigations/>
                    {props?.map((item) => (
                        <SwiperSlide key={item.id} className='swiper__slide'>
                          <Link to={item.link}>
                            <div className="choice__item schadowImg" style={{ backgroundImage: `url(${item.img})` }}>
                                <h3>{item.title}</h3>
                            </div>
                          </Link>
                        </SwiperSlide>
                    ))}
        </Swiper>
        </div>
    )
};
const SwiperNavigations = () => {
    const swiper = useSwiper();
    return (
      <div className="navigation-btns nav__button">
        <button onClick={() => swiper.slidePrev()} ><img className='buttonPrev' src={nextArrow} alt="" /></button>
        <button onClick={() => swiper.slideNext()} ><img className='buttonNex' src={prewArrow} alt="" /></button>
      </div>
    );
  };