

import { Link } from "react-router-dom";
import React from 'react';

// componets
import Choice from "../../section/choice/Сhoice";
import RecommendationSwiper from "../../section/recommendationSwiper/RecommendationSwiper.jsx";
import About from "../../section/about/About.jsx";
import Aside from "../../section/aside/Aside.jsx";
import HomeSwiper from "../../section/homeSwiper/HomeSwiper.jsx";

//obj
// import objNewsSwiper from "../../asset/obj/newsSwiper.js";
import objChoice from "../../asset/obj/choice.js";
import objFoodSwiper from "../../asset/obj/eda/edaRecomend.js";
import objKinoSwiper from "../../asset/obj/kino/kinoRecomend.js";
import objZdorovieSwiper from "../../asset/obj/zdorovie/zdorovieRecomend.js";
import objZagorodomSwiper from "../../asset/obj/zagorodom/zagorodomRecomend.js";
import objDetiSwiper from "../../asset/obj/deti/detiRecomend.js";
import objObrazovanieSwiper from "../../asset/obj/obrazovanie/ObrazovanieRecomend.js";
import objRazvlechenieSwiper from "../../asset/obj/razclechenie/razvlechenieRecomend.js";
import objKulturaSwiper from "../../asset/obj/kultura/kulturaRecomend.js";
import objRecommendationSwiper from "../../asset/obj/recommendationSwiper.js";
import objAside from "../../asset/obj/aside.js";

// style
import "./home.scss";
import "../commonStyle.scss";

import {objHome} from "../../asset/obj/home/objHome.js";

export default function Home(){
    const objHome_1 = objHome[0];
    const objHome_2 = objHome[1];
    const objHome_3 = objHome[2];
    const objHome_4 = objHome[3];
    const objHome_5 = objHome[4];
    const objHome_6 = objHome[5];
    const objHome_7 = objHome[6];
    const objHome_8 = objHome[7];
    const objHome_9 = objHome[8];
    return(
        <div className="center__container _container">

            <section className="home__container">
                <div className="home__containt">
                    <div className="home__containt__main _body__box__shadow">
                    <div className="home__header"><h4>Куда сходить в <span>Димитровграде</span></h4></div>
                    <div className="home__slider">
                        <div className="home__slider_1 schadowImg"><Link to={objHome_1.link}><img src={objHome_1.img} alt="" /><h3>{objHome_1.name}</h3></Link></div>
                        <div className="home__slider_2 schadowImg"><Link to={objHome_2.link}><img src={objHome_2.img} alt="" /><h3>{objHome_2.name}</h3></Link></div>
                        <div className="home__slider_3 schadowImg"><Link to={objHome_3.link}><img src={objHome_3.img} alt="" /><h3>{objHome_3.name}</h3></Link></div>
                        <div className="home__slider_4 schadowImg"><Link to={objHome_4.link}><img src={objHome_4.img} alt="" /><h3>{objHome_4.name}</h3></Link></div>
                        <div className="home__slider_5 schadowImg"><Link to={objHome_5.link}><img src={objHome_5.img} alt="" /><h3>{objHome_5.name}</h3></Link></div>
                        <div className="home__slider_6 schadowImg"><Link to={objHome_6.link}><img src={objHome_6.img} alt="" /><h3>{objHome_6.name}</h3></Link></div>
                        <div className="home__slider_7 schadowImg"><Link to={objHome_7.link}><img src={objHome_7.img} alt="" /><h3>{objHome_7.name}</h3></Link></div>
                        <div className="home__slider_8 schadowImg"><Link to={objHome_8.link}><img src={objHome_8.img} alt="" /><h3>{objHome_8.name}</h3></Link></div>
                        <div className="home__slider_9 schadowImg"><Link to={objHome_9.link}><img src={objHome_9.img} alt="" /><h3>{objHome_9.name}</h3></Link></div>
                    </div>
                    </div>
                        <Choice props={objChoice}/>
                        {/* <HomeSwiper props={objNewsSwiper} title="Новости димитровграда"/> */}
                        <HomeSwiper props={objFoodSwiper} title="Где покушать"/>
                        <HomeSwiper props={objRazvlechenieSwiper} title="Где найти развлечения"/>
                        <HomeSwiper props={objKinoSwiper} title="Кино в димитровграде"/>
                        <HomeSwiper props={objZdorovieSwiper} title="Здоровый образ жизни"/>
                        <HomeSwiper props={objZagorodomSwiper} title="За городом"/>
                        <HomeSwiper props={objDetiSwiper} title="Для детей"/>
                        <HomeSwiper props={objObrazovanieSwiper} title="Образование"/>

                        <HomeSwiper props={objKulturaSwiper} title="Культурные места"/>

                        <RecommendationSwiper props={objRecommendationSwiper}/>
                        <About/>
                    </div>
            </section>
            <section className="aside__container">
                <Aside props={objAside}/>
            </section>
        </div>
    )
}